.nav-bar{
    background-color: #181818;
    width: 100%;
    height: 55px;
    position: absolute;
    top: 0%;
    z-index: 3;
    min-height: 55px;
    


    nav {
        display: inline-flex;
        flex-direction: row;
        align-items: baseline;
        vertical-align: top;
        flex-shrink: 1;
        margin-top: 15px;
        width: 60%;
        min-width: 5%;
        margin-left: 5%;
        :hover{
            color: #7655ec;
        }

        a.active {
            color: #7655ec;
        }
        
    
    
        a { 
            display: flex;
            margin-left: 3px;
            width: 33%;
            flex-shrink: 3;
            font-size: 24px;
            text-decoration: none;
            align-items: center;
            color: #ffffff;
            :hover{
                color: #7655ec;
            }
            i {
                transition: all 0.3s ease-out;
            }
        }

        .nav-text{
            margin-left: 10px;
            margin-top: -5px;
            font-size: 1vw;
            font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
            align-content: baseline;
            align-items: baseline;
        }

    }

    ul {
        position: absolute;
        float: right;
        width: 15%;
        margin-left: 12%;
        margin-right: 1%;
        margin-top: 15px;
        height: 20px;
        align-items: baseline;
        display: inline-flex;
        flex-wrap: wrap;
        gap: 10px;
        flex-shrink: 1;
        flex-direction: row;
        list-style-type: none;
        font-family:'WixMadeforDisplay';

        


        li {
            display: inline-flex;
            align-items: baseline;
            position: relative;
            font-size: 24px;
            text-decoration: none;
            text-overflow: ellipsis;
            

            &:hover svg{
                color: #7655ec;
    
            }

            

            
            
        }

        

        
    }

   

}


